@import "../globalFontStyling.scss";


.likesWrapper{
display:block;
}

.likesSubWrapper{
display: inline-block;
}


.likeButton{
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 3px 10px 3px 10px;
  border-radius: 12px;
  border: 3px $colorBlueGrey solid;
  font-weight: bold;

  transition : 200ms;

  &:hover{
    border-color: $colorBluer;

    .plusOneLike{
      border-color: $colorBluer;
      color : $colorBluer;
    }
  }

}


.likeIcon{
  max-width: 17px;
}


.likesNumber{
  font-size: $fontsizeSmall;
  margin-left: 10px;
  padding-right: 10px;
}


.plusOneLike{
  padding-left: 10px;
  font-size: $fontsizeSmall;
  border-left: solid 3px $colorBlueGrey;

  transition : 200ms;

}