@import "../globalFontStyling";


.demoLinkButton {
  background-color: inherit;
  padding: 0 22px 0 22px;
  height: 26.2px; //same height as ProjectGithubLinkButton
  border: 3px solid;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border-radius: 14px;
  font-size: $fontsizeSmall;
  transition: color , background-color 0.2s;

  &:hover{
    box-shadow: 0px 0px 5px $colorBluer;
  }
}