@import "../globalFontStyling";

.buttonContainer {
  background-color: #EEEEEE;
  display: inline-block;
  border-radius: 14px;

  cursor: pointer;
  margin-top: 10px;


  &:hover {
    box-shadow: $colorGrey 0px 0px 8px;
  }
}

.socialNetworkingIcon {
  width: 20px;
}

.socialNetworkingButton {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 2px 10px 1px 10px;

  font-size: $fontsizeSmall;
  font-weight: bold;
}

.socialNetworkingButton div {
  padding: 0 6px 0 6px;
}

